
import {mapGetters} from 'vuex'
import Toast from 'vux/src/components/toast/index.vue'


export default {
computed: mapGetters([]),
components: {
Toast
},
data() {
return {
typeV:'password',
toastValue: '手机号不能为空！',
type: 'warn',
showSuccess:false,
showPositionValue: false,
options:{
"loginUserID": "huileyou",
"loginUserPass": "123",
"operateUserID": "",
"operateUserName": "",
"pcName": "",
"token": "",
"userID": "",//用户编码
"password": ""//密码
}
}
},
methods: {

look(){
if(this.typeV== 'text'){
this.typeV= 'password'
}else{
this.typeV= 'text'
}
},

goRegister() {
this.$router.push({name: 'Register'})
},

login(){
if (!this.options.userID) {
this.showPositionValue = true;
return
}
if (!this.options.password) {
this.toastValue = '密码不能为空'
this.showPositionValue = true;
return
}
this.$store.dispatch('loginSubmit',this.options)
.then((suc)=>{

this.showSuccess = true;
setTimeout(()=>{
this.$router.push({name: 'Home'})
},1000)
}, err => {
this.type = 'warn';
this.toastValue = err
this.showPositionValue = true;
})
},

goForgetPassword(){
this.$router.push({name: 'ForgetPassword'})
},
initData() {
},
search() {
this.initData()
}
},
}



